<template>
<div>
  <div class="row">    
        <info-chart
            v-if="loaded_incomplete"
            icon="icon-lg pe-7f-cart"
            bgclass="bg-flat-color-6"
            :counter="this.total"
            title="Total Requests"
            
        >
        </info-chart>
        <info-chart-two
            v-if="loaded_complete"
            icon="icon-lg pe-7f-cart"
            bgclass="bg-flat-color-3"
            :counter="this.complete"
            title="Completed Tasks"
        >
        </info-chart-two>
    </div>
 <div data-app class="table-responsive">
    <v-data-table
      :headers="headers"
      :items="pooldata"
      class="elevation-1"
      :rows-per-page-items="[20]"
      disable-initial-sort
    >
      <template v-slot:items="props">
      <td style='width:5em'>{{ props.item.item_id }}</td>
      <td>{{ props.item.Req_Name }}</td>
      <td style='width:55em'>{{ props.item.Description }}</td>
      <td>{{ props.item.Location }}</td>
      <td>{{ props.item.Due_date }}</td>
      <td style='width:5em'>{{ props.item.Due_time }}</td>
      <td>{{ props.item.Handler }}</td>
      <td>{{ props.item.Status }}</td>
      <td>{{ props.item.Request_for }}</td>
    
      <template v-slot:pageText="props">
        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
      </template>
    
    </template>
    </v-data-table>
</div>
</div>
</template>

<script>
import Info from '../views/dashboard/InfoBox.vue';
import InfoChart from '../views/dashboard/InfoBoxChart.vue';
import InfoChartTwo from '../views/dashboard/InfoBoxChart2.vue';
import axios from 'axios';

export default {
    data(){
        return{
            search: '',
            pooldata : [],
            userid:"",
            total:0,
            complete:0,
            incomplete:0,
            loaded:false,
            loaded_complete:false,
            loaded_incomplete:false,
        headers: [
          {text: 'ID',value :'item_id',sortable: true},
          { text: 'Requester',value:'Req_Name',sortable: true},
          { text: 'Description',value:'Description',sortable: true},
          { text: 'Location',value:'Location',sortable: true},
          { text: 'Due date',value:'Due_date',sortable: true},
          { text: 'Due Time',value:'Due_time',sortable: false},
          { text: 'Assigned to',value:'Handler',sortable: true},
          { text: 'Issue Status',value:'Status',sortable: true},
          { text: 'Request For',value:'Request_for',sortable: true}
        ],
        }            
    },
    components: {
        Info,
        InfoChart,
        InfoChartTwo
    },
    
    async created(){
        await axios.get('RequestPortal/backend/request_pool.php')
        .then(
            response=>{
                this.pooldata = response.data
            }
        );
        await axios.get('RequestPortal/backend/fetchtotal.php')
           .then((response) => {
            this.total = response.data.total;
            this.loaded = true;
        });
        await axios.get('RequestPortal/backend/fetch_complete.php')
           .then((response_complete) => {
            this.complete = response_complete.data.total;
            this.loaded_complete = true;
        });
        await axios.get('RequestPortal/backend/fetch_incomplete.php')
           .then((response_incomplete) => {
            this.incomplete = response_incomplete.data.total;
            this.loaded_incomplete = true;
        })
    }
}
</script>




